.code-input,.code-output {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: none;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 0.8rem;
  background: transparent;
  white-space: pre;
  line-height: 1.5em;
  overflow-y: hidden;
}
.code-input {
  padding: 0px 0px 0px 2px;
  opacity: 0.7;
  margin: 0;
  color: #999;
  resize: none;
}
.code-output {
  pointer-events: none;
  z-index: 3;
  margin: 0;
}
.code-output code {
  padding: 0px 0px 0px 2px;
  /* position: absolute; */
  display: block;
  color: #f4f4f4;
  font-size: 0.8rem;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}
a.token {
  text-decoration: none;
} 
.token.selector,
.token.keyword,
.token.punctuation {
  color:PaleVioletRed;
}
.token.property,
.token.number,
.token.string,
.token.tag-id,
.token.punctuation{
  color:DodgerBlue;
}
.token.function,
.token.attr-name {
  color: cadetblue;
}
.token.atrule .atrule-id {
  color: blueviolet;
}
.token.boolean {
  color: LightSlateGray;
}
.token.comment {
  color: darkgray;
}
.variable {
  color: cadetblue;
}